"use client";

import { useEffect, useState } from "react";

export function Countdown({ targetDate }: { targetDate: Date }) {
  const [timeLeft, setTimeLeft] = useState({
    days: 0,
    hours: 0,
    minutes: 0,
    seconds: 0,
    isToday: false,
  });

  useEffect(() => {
    const calculateTimeLeft = () => {
      const now = new Date();
      const difference = targetDate.getTime() - now.getTime();

      if (difference <= 0) {
        setTimeLeft({ days: 0, hours: 0, minutes: 0, seconds: 0, isToday: true });
        return;
      }

      const days = Math.floor(difference / (1000 * 60 * 60 * 24));
      const hours = Math.floor((difference / (1000 * 60 * 60)) % 24);
      const minutes = Math.floor((difference / 1000 / 60) % 60);
      const seconds = Math.floor((difference / 1000) % 60);

      setTimeLeft({ days, hours, minutes, seconds, isToday: false });
    };

    calculateTimeLeft();
    const timer = setInterval(calculateTimeLeft, 1000);

    return () => clearInterval(timer);
  }, [targetDate]);

  if (timeLeft.isToday) {
    return (
      <div className="rounded-3xl bg-gradient-to-r from-christmas-berry via-christmas-wine to-christmas-berry p-8 text-center shadow-2xl ring-2 ring-christmas-sand/50 sm:p-12 lg:p-16">
        <div className="mb-4 text-6xl sm:text-7xl lg:text-8xl">üéâ</div>
        <p className="text-3xl font-black text-christmas-sand sm:text-4xl lg:text-6xl xl:text-7xl">
          ¬°Hoy es el d√≠a!
        </p>
        <p className="mt-3 text-xl font-bold text-christmas-sand/90 sm:text-2xl lg:text-3xl">
          Nos vemos esta noche üéÑ
        </p>
      </div>
    );
  }

  return (
    <div className="grid grid-cols-2 gap-3 sm:grid-cols-4 sm:gap-4 lg:gap-6">
      <div className="group relative overflow-hidden rounded-2xl bg-gradient-to-br from-christmas-berry to-christmas-wine p-5 shadow-xl ring-1 ring-christmas-sand/25 transition hover:scale-105 hover:shadow-2xl sm:rounded-3xl sm:p-6 lg:p-8">
        <div className="absolute -top-3 -right-3 text-3xl opacity-70 transition group-hover:scale-110 sm:text-4xl lg:text-5xl">üéÅ</div>
        <div className="absolute inset-0 bg-gradient-to-t from-black/10 to-transparent opacity-0 transition group-hover:opacity-100" />
        <p className="relative text-4xl font-black text-christmas-sand sm:text-5xl lg:text-6xl xl:text-7xl">
          {timeLeft.days}
        </p>
        <p className="relative mt-2 text-xs font-semibold uppercase tracking-wider text-christmas-sand/80 sm:text-sm lg:text-base">
          D√≠as
        </p>
      </div>
      
      <div className="group relative overflow-hidden rounded-2xl bg-gradient-to-br from-christmas-pine to-christmas-forest p-5 shadow-xl ring-1 ring-christmas-sand/25 transition hover:scale-105 hover:shadow-2xl sm:rounded-3xl sm:p-6 lg:p-8">
        <div className="absolute -top-3 -right-3 text-3xl opacity-70 transition group-hover:scale-110 sm:text-4xl lg:text-5xl">‚≠ê</div>
        <div className="absolute inset-0 bg-gradient-to-t from-black/10 to-transparent opacity-0 transition group-hover:opacity-100" />
        <p className="relative text-4xl font-black text-christmas-sand sm:text-5xl lg:text-6xl xl:text-7xl">
          {timeLeft.hours}
        </p>
        <p className="relative mt-2 text-xs font-semibold uppercase tracking-wider text-christmas-sand/80 sm:text-sm lg:text-base">
          Horas
        </p>
      </div>
      
      <div className="group relative overflow-hidden rounded-2xl bg-gradient-to-br from-christmas-wine to-christmas-berry p-5 shadow-xl ring-1 ring-christmas-sand/25 transition hover:scale-105 hover:shadow-2xl sm:rounded-3xl sm:p-6 lg:p-8">
        <div className="absolute -top-3 -right-3 text-3xl opacity-70 transition group-hover:scale-110 sm:text-4xl lg:text-5xl">üîî</div>
        <div className="absolute inset-0 bg-gradient-to-t from-black/10 to-transparent opacity-0 transition group-hover:opacity-100" />
        <p className="relative text-4xl font-black text-christmas-sand sm:text-5xl lg:text-6xl xl:text-7xl">
          {timeLeft.minutes}
        </p>
        <p className="relative mt-2 text-xs font-semibold uppercase tracking-wider text-christmas-sand/80 sm:text-sm lg:text-base">
          Min
        </p>
      </div>
      
      <div className="group relative overflow-hidden rounded-2xl bg-gradient-to-br from-christmas-forest to-christmas-pine p-5 shadow-xl ring-1 ring-christmas-sand/25 transition hover:scale-105 hover:shadow-2xl sm:rounded-3xl sm:p-6 lg:p-8">
        <div className="absolute -top-3 -right-3 text-3xl opacity-70 transition group-hover:scale-110 sm:text-4xl lg:text-5xl">‚ú®</div>
        <div className="absolute inset-0 bg-gradient-to-t from-black/10 to-transparent opacity-0 transition group-hover:opacity-100" />
        <p className="relative text-4xl font-black text-christmas-sand sm:text-5xl lg:text-6xl xl:text-7xl">
          {timeLeft.seconds}
        </p>
        <p className="relative mt-2 text-xs font-semibold uppercase tracking-wider text-christmas-sand/80 sm:text-sm lg:text-base">
          Seg
        </p>
      </div>
    </div>
  );
}
