"use client";

import { useState } from "react";

const items = [
  { emoji: "ü•§", name: "Gaseosa", desc: "Bebidas para todos" },
  { emoji: "üç∞", name: "Panet√≥n", desc: "El cl√°sico navide√±o" },
  { emoji: "üçÆ", name: "Postre", desc: "Algo dulce" },
  { emoji: "üßä", name: "Hielo", desc: "Para las bebidas" },
  { emoji: "üçï", name: "Pizza", desc: "Para picar" },
  { emoji: "üç™", name: "Galletas", desc: "Dulces o saladas" },
  { emoji: "ü•ó", name: "Ensalada", desc: "Algo fresco" },
  { emoji: "üçæ", name: "Champagne", desc: "Para brindar" },
];

export function Roulette() {
  const [result, setResult] = useState<typeof items[0] | null>(null);
  const [spinning, setSpinning] = useState(false);
  const [currentItem, setCurrentItem] = useState<typeof items[0] | null>(null);

  const spin = () => {
    if (spinning) return;
    setSpinning(true);
    setResult(null);

    // Animaci√≥n de cambio r√°pido
    let count = 0;
    const interval = setInterval(() => {
      setCurrentItem(items[Math.floor(Math.random() * items.length)]);
      count++;
      if (count > 15) {
        clearInterval(interval);
      }
    }, 100);

    setTimeout(() => {
      const randomItem = items[Math.floor(Math.random() * items.length)];
      setResult(randomItem);
      setCurrentItem(null);
      setSpinning(false);
    }, 2000);
  };

  return (
    <div className="mx-auto max-w-2xl space-y-6 text-center sm:space-y-8 lg:space-y-10">
      {/* Mostrar items disponibles */}
      <div className="grid grid-cols-4 gap-2 sm:gap-3 lg:gap-4">
        {items.map((item, idx) => (
          <div
            key={idx}
            className="flex flex-col items-center justify-center rounded-xl bg-christmas-forest/50 p-3 ring-1 ring-christmas-sand/20 sm:rounded-2xl sm:p-4 lg:p-5"
          >
            <span className="text-2xl sm:text-3xl lg:text-4xl">{item.emoji}</span>
            <span className="mt-1 text-xs font-bold text-christmas-sand/90 sm:text-sm lg:text-base">
              {item.name}
            </span>
          </div>
        ))}
      </div>

      {/* Vista de spinning */}
      {spinning && currentItem && (
        <div className="animate-pulse rounded-2xl bg-christmas-wine/70 p-6 shadow-xl ring-2 ring-christmas-sand/40 sm:rounded-3xl sm:p-8 lg:p-12">
          <div className="text-6xl sm:text-7xl lg:text-8xl">{currentItem.emoji}</div>
          <p className="mt-3 text-xl font-black text-christmas-sand sm:mt-4 sm:text-2xl lg:text-3xl">
            {currentItem.name}
          </p>
        </div>
      )}

      {/* Bot√≥n */}
      <button
        onClick={spin}
        disabled={spinning}
        className="w-full rounded-2xl bg-christmas-berry px-8 py-4 text-lg font-black text-christmas-sand shadow-xl ring-1 ring-christmas-sand/30 transition hover:scale-105 hover:bg-christmas-wine focus:outline-none focus:ring-2 focus:ring-christmas-sand/50 disabled:cursor-not-allowed disabled:opacity-60 sm:w-auto sm:rounded-3xl sm:px-12 sm:py-6 sm:text-2xl lg:px-16 lg:py-8 lg:text-3xl"
      >
        {spinning ? "üé∞ Girando..." : "üéØ Girar la ruleta"}
      </button>

      {/* Resultado */}
      {result && !spinning && (
        <div className="animate-[bounce_0.5s_ease-in-out] rounded-2xl bg-christmas-pine p-6 shadow-2xl ring-2 ring-christmas-sand/40 sm:rounded-3xl sm:p-10 lg:p-14">
          <div className="mb-3 text-5xl sm:mb-4 sm:text-6xl lg:mb-6 lg:text-7xl">üéä</div>
          <p className="text-xl font-black text-christmas-sand sm:text-2xl lg:text-4xl">
            ¬°Te toca llevar!
          </p>
          <div className="my-4 sm:my-6 lg:my-8">
            <div className="text-6xl sm:text-7xl lg:text-8xl">{result.emoji}</div>
            <p className="mt-3 text-3xl font-black text-christmas-sand sm:mt-4 sm:text-4xl lg:text-6xl">
              {result.name}
            </p>
            <p className="mt-2 text-base font-semibold text-christmas-sand/85 sm:text-lg lg:text-xl">
              {result.desc}
            </p>
          </div>
          <p className="text-sm text-christmas-sand/80 sm:text-base lg:text-lg">
            Si ya tienes algo, ignora la ruleta üòÇ
          </p>
          <button
            onClick={spin}
            className="mt-4 rounded-xl bg-christmas-wine px-6 py-3 text-sm font-black text-christmas-sand shadow-lg transition hover:bg-christmas-berry focus:outline-none focus:ring-2 focus:ring-christmas-sand/50 sm:mt-6 sm:rounded-2xl sm:px-8 sm:py-4 sm:text-base lg:text-lg"
          >
            üîÑ Volver a girar
          </button>
        </div>
      )}
    </div>
  );
}
